.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 97.4%;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); 
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1); 
  z-index: 1000; 
}

.logo {
  color: #E0E0E0;
  font-size: 1.2rem;
  font-weight: bold;
  margin-top: 30px;
}

.nav-links {
  display: flex;
  flex-wrap: wrap;
}

.nav-links a {
  color: #FBFBFB;
  margin-left: 15px;
  text-decoration: none;
  font-size: 1rem;
  padding: 8px 12px;
  border-radius: 5px; 
  transition: color 0.3s ease, background-color 0.3s ease;
}

.nav-links a:hover {
  color: #E0E0E0;
  background-color: rgba(255, 255, 255, 0.2); 
}

/* Sidebar Styles for Smaller Screens */
.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  width: 250px; 
  height: 100%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background: rgba(255, 255, 255, 0.1); 
  backdrop-filter: blur(10px); 
  border-right: 1px solid rgba(255, 255, 255, 0.2); 
  box-shadow: 4px 0 15px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  transform: translateX(-100%);
  transition: transform 0.3s ease; 
}

.sidebar.show {
  transform: translateX(0);
}

.sidebar .nav-links {
  flex-direction: column;
  width: 100%;
}

.sidebar .nav-links a {
  margin: 10px 0;
  width: 100%;
  text-align: left; 
}

/* Hamburger Icon for Smaller Screens */
.hamburger {
  display: none;
}

/* Responsive Design for Smaller Screens */
@media (max-width: 768px) {
  .navbar {
    display: none; 
  }

  .sidebar {
    display: flex; 
  }

  .hamburger {
    display: flex; 
    position: fixed;
    top: 10px;
    left: 15px;
    flex-direction: column;
    cursor: pointer;
    z-index: 1001; 
  }

  .hamburger div {
    width: 25px;
    height: 3px;
    background-color: #E0E0E0;
    margin: 4px 0;
    transition: 0.4s;
  }

  /* Transformations for the hamburger icon to become an "X" */
  .hamburger.open div:nth-child(1) {
    transform: rotate(-45deg) translate(-5px, 6px);
  }

  .hamburger.open div:nth-child(2) {
    opacity: 0;
  }

  .hamburger.open div:nth-child(3) {
    transform: rotate(45deg) translate(-5px, -6px);
  }
}
