body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #1B1B1E;
}

.top-section {
  display: flex;
  justify-content: center;
  gap: 30px;
  margin-top: 120px;
}

@media (max-width: 1600px) {
  .top-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

@media (max-width: 768px) {
  .top-section {
    margin-top: 55px;
  }
}

